import "bootstrap/dist/css/bootstrap.min.css";
import {Link} from 'react-router-dom'
import './shop.css'
import ShoppingCart from '../shopping-cart.svg'
import {Link as ScrollLink} from 'react-scroll'
import {
  Navbar,
  Nav,
  Image
} from "react-bootstrap";
import cart from './cart.png'
function App(){
return(
<Navbar collapseOnSelect expand="lg" variant="dark" style={{ backgroundColor: "black" }} sticky="top">
<Navbar.Brand 
      className='navBrand'  >
        
          <Link to='/' ><Image  style={{height:'40px',width:'auto'}} src={`https://cdn.shopify.com/s/files/1/0062/9587/9780/files/Logo_Final_1coffeewhite_copy_x100.png?v=1596805614`}/></Link>
        </Navbar.Brand>
        <Navbar.Brand

        className='shopNavCart'
        style={{marginLeft:'90vw',position:'absolute'}} 
        
        >
          <Link to='/checkout'>   <Image  style={{height:'35px',width:'auto'}} src={cart}/></Link>
        </Navbar.Brand>
<Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse >
<Nav >
  <Nav.Link>
<ScrollLink to='WhyRage'   
        spy={true} 
        smooth={true} 
        duration={200} 
        style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Why Rage?</ScrollLink></Nav.Link>
        <Nav.Link>
        <ScrollLink to='Science'   
        spy={true} 
        smooth={true} 
        duration={200} 
        style={{ color: "grey",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Science
        </ScrollLink></Nav.Link>
          <Nav.Link  style={{textAlign:'center'}} > <Link to='/shop'  style={{ color: "white",fontFamily: "Abril Fatface , cursive",textDecoration:'none'}}>Shop</Link></Nav.Link> 
          <Nav.Link className='cart-text' style={{textAlign:'center'}} ><Link to='/checkout'  style={{ color: "grey", fontFamily: "Abril Fatface, cursive",textDecoration:'none'}}>Cart </Link></Nav.Link>
        </Nav> 
        </Navbar.Collapse>
      </Navbar>
      )
            }
      export default App