import {
  Button,
  Card,
  Container,
  Col,
  Row,
} from "react-bootstrap"
import rage from './flavor.mp4'
import ReactPlayer from 'react-player'
function App(){
    return(
<Container fluid  style={{backgroundColor:'black',padding:0}}>
    <Row noGutters style={{margin:0}}>
        <Col lg style={{padding:0}} >
        <Card style={{background:'black',color:'white'}}>
                <Card.Body >     
                <Card.Title className='flavor-title' style={{textAlign:'left',marginLeft:'10%'}}>
                    <div style={{width:'50px',height:'10px',borderTop:'2px solid purple',marginLeft:'10%'}}></div>
                    Brand New Flavors Get Hooked.</Card.Title>
                <Card.Text style={{padding:'50px',color:"grey",paddingRight:'30%'}}>
                Fuel your day with four brand new flavours. Whether youre kick-starting your morning or recharging yourself, these delicious flavoured coffee jars will keep your caffeine experience at its peak.
                </Card.Text>
                <Button style={{border:'1px solid 	purple',color:'	purple',background:'inherit',borderRadius:'0%',padding:'10px',marginLeft:'10%'}}>Buy Now!</Button>
            </Card.Body>
        </Card>
        </Col>
         <Col style={{padding:0}}  > 
        <ReactPlayer id='flavor_container' height='auto' width='100%'   playing={true} controls={false} muted playbackRate='0.91' loop  url={rage}/>
        </Col></Row> 
</Container>
    )
}
export default App;