import * as actionTypes from './action'
const InitialState={
    product:[
        {
            id:1,
            url:"https://cdn.shopify.com/s/files/1/0062/9587/9780/products/IMG_38001_540x.jpg?v=1607504209",
            description:" Creamy, Sweet,Nutty Most Popular One!",
            Title:"Creme Caramel (100gms)",
            price:450
        },
        {
            id:2,
            url:"https://cdn.shopify.com/s/files/1/0062/9587/9780/products/IMG_38131_540x.jpg?v=1607504209",
            description:" Perfect if You Love Chocolate",
            Title:"Dark Chocolate (100gms)",
            price:450
        },
        {
            id:3,
            url:"https://cdn.shopify.com/s/files/1/0062/9587/9780/products/IMG_38131_540x.jpg?v=1607504209",
            description:"Zaps Freshness and Energy into you!",
            Title:"Sparky Orange (100gms)",
            price:450
        }
    ],
    cart:[]
}
const reducer=(state=InitialState,action)=>{
    switch(action.type){
        case actionTypes.addItem:
            //action.payload will be the product id that we are adding in the cart 
            const item=state.product.find(item=>item.id===action.payload.id)
            //check if its already in cart
            const Incart=state.cart.find(item=>item.id===action.payload.id ? true : false)
            return{ ...state,
            cart:Incart ? state.cart.map(item=>item.id===action.payload.id ? {...item,qty:item.qty+1}:item) :[...state.cart,{...item,qty:1}]};
            case actionTypes.removeItem:
                return{ ...state,
                    cart:state.cart.filter(item=>item.id !== action.payload.id)
                 };  
            case actionTypes.adjustQty:
                return{ ...state,
                    cart:state.cart.map(item=>item.id===action.payload.id ? {...item,qty:action.payload.qty}:item)
                }; 
            default:
             return {...state}; 
    }
}
export default reducer;