export const addItem='additem'
export const removeItem='removeitem'
export const adjustQty ='adjustQty'
export const AddItem=(itemId)=>{
    return{
        type:addItem,
        payload:{
            id:itemId
        }
    }
} 
export const RemoveItem=(itemId)=>{
    return{
        type:removeItem,
        payload:{
            id:itemId
        }
    }
} 
export const AdjustQty=(itemId,qty)=>{
    return{
        type:adjustQty,
        payload:{
            id:itemId,
            qty:qty
        }
    }
} 