import React,{useState} from 'react'
import { Card,Button } from 'react-bootstrap'
import {useDispatch } from 'react-redux'
import {RemoveItem,AdjustQty } from '../redux/action'

import './shop.css'
function App({Item}){
  const dispatch=useDispatch()
  const [qty, setqty] = useState(Item.qty)
  const changeHandler=(e)=>{
   setqty(e.target.value)
    dispatch(AdjustQty(Item.id,e.target.value))
  }
    return(
        <>
         <Card border='' fluid style={{backgroundColor:'#16161d',color:'white',border:'none',width:'60%'}} >
    <Card.Body>
      <Card.Title style={{color:'purple'}}>{Item.Title}</Card.Title>
      <Card.Text>
      {Item.description}
      <br/>
     {Item.price}
     RS 
     <lable htmlFor='qty'>X  </lable>
     <input style={{background:'inherit',fontSize:'17px',color:'white',border:'none'}} onChange={changeHandler} min='1'  type='number' value={qty} /> 
      <Button style={{marginTop:'10px',textTransform:'uppercase',backgroundColor:'black',border:'1px solid purple',borderRadius:'0',color:'purple'}} onClick={()=>dispatch(RemoveItem(Item.id))}> Remove From Cart</Button>
    
      </Card.Text>
    </Card.Body>
  </Card>
  <br/>
  <br />
        </>
    )
}
export default App;