import React from 'react'
import { Container,Row,Card,CardDeck,Button,Col } from 'react-bootstrap'
import './shop.css'
import {connect,useSelector} from 'react-redux'
import Cart from './cart1'
import {AddItem} from '../redux/action'

function App({AddToCart}){
  const cart = useSelector(state => state.cart)
return(
    <>
    <Container  >
        <Row >
  <Col className='cardCol' sm={12} xs={12} md={4} lg={4} >
  
  <Card  style={{backgroundColor:'#16161d',color:'white',border:'none'}} >
    <Card.Img className='Cardimg'  src="https://cdn.shopify.com/s/files/1/0062/9587/9780/products/IMG_38001_540x.jpg?v=1607504209" />
    <Card.Body>
      <Card.Title  style={{color:'purple'}}>Creme Caramel (100gms)</Card.Title>
      <Card.Text>
      Creamy, Sweet, Most Popular One!
      </Card.Text>
      <Button onClick={()=>AddToCart(1)}  style={{marginTop:'10px',textTransform:'uppercase',backgroundColor:'black',border:'1px solid purple',borderRadius:'0',color:'purple'}} >Add To Cart</Button>{' '}
    </Card.Body>
  </Card>
  </Col>
  <br/>
  <Col className='cardCol' sm={12} xs={12} md={4} lg={4} >
  <Card  style={{backgroundColor:'#16161d',color:'white',border:'none'}} >
    <Card.Img className='Cardimg' style={{height:'450px'}} src="https://cdn.shopify.com/s/files/1/0062/9587/9780/products/IMG_38131_540x.jpg?v=1607504209" />
    <Card.Body>
      <Card.Title  style={{color:'purple'}}>Sparky Orange (100gms)</Card.Title>
      <Card.Text>
      Zaps energy into you!
      </Card.Text>
      <Button onClick={()=>AddToCart(2)} style={{marginTop:'10px',textTransform:'uppercase',backgroundColor:'black',border:'1px solid purple',borderRadius:'0',color:'purple'}} >Add To Cart</Button>{' '}
    </Card.Body>
  </Card>
  </Col>
  <br/>
  <Col className='cardCol' sm={12} xs={12} md={4} lg={4} >
  <Card  style={{backgroundColor:'#16161d',color:'white',border:'none'}} >
    <Card.Img className='Cardimg' style={{height:'450px'}}  src="https://cdn.shopify.com/s/files/1/0062/9587/9780/products/IMG_38281_540x.jpg?v=1607504209" />
    <Card.Body>
      <Card.Title  style={{color:'purple'}}>Dark Chocolate (100gms)</Card.Title>
      <Card.Text>
      Perfect If You  love chocolate
      </Card.Text>
      <Button onClick={()=>AddToCart(3)}  style={{marginTop:'10px',textTransform:'uppercase',backgroundColor:'black',border:'1px solid purple',borderRadius:'0',color:'purple'}} >Add To Cart</Button>{' '}
    </Card.Body>
  </Card>
  </Col>
</Row>
</Container>
<br/>
<br/>
<br/>


</>
)
}

const MapDispatchToProps=(dispatch)=>{
  return{
AddToCart:(id)=>dispatch(AddItem(id))
  }
}
export default connect(null,MapDispatchToProps) (App);
