import React from 'react'
import { Col, Row,Container,Card,Nav,Form } from 'react-bootstrap'
function App(){
    return(
        <div  style={{position:'absolute',width:"100%",backgroundColor:'#b19cd9'}} >
            <Container fluid>
                <Row>
                
  <Col>
  <Nav  className="flex-column" >
  <Nav.Link  href="ragecoffee.com/blogs/rage-coffee-blog"><span style={{color:'black'}}>Blogs</span></Nav.Link>
  <Nav.Link  href="ragecoffee.com/blogs/rage-coffee-blog"><span style={{color:'black'}}>Customer Care</span></Nav.Link>
  <Nav.Link  href="ragecoffee.com/blogs/rage-coffee-blog"><span style={{color:'black',}}>Check If we Are Open Near You?</span></Nav.Link>
  <Nav.Link  href="ragecoffee.com/blogs/rage-coffee-blog"><span style={{color:'black'}}>Nutritional Info</span></Nav.Link>
  <Nav.Link  href="ragecoffee.com/blogs/rage-coffee-blog"><span style={{color:'black'}}>Apply As a Employee</span></Nav.Link>
</Nav>
  </Col>
  <Col>
    <Card
    style={{ backgroundColor:'inherit',border:'none' }}
    className="mb-2"
  >
    <Card.Body style={{color:'black'}} >
      <Card.Title><span style={{ color: "black" }}> Become A Rager,Subscribe</span> </Card.Title>
      <Card.Text>
      <Form>
  <Form.Group controlId="exampleForm.ControlInput1">
    <Form.Label>Email address</Form.Label>
    <Form.Control style={{background:'inherit', borderBottom:'1px solid black',borderTop:'none',borderLeft:'none',borderRight:'none',borderRadius:'0'}}  type="email" placeholder="name@example.com" />
  </Form.Group>
</Form>
          <br/>
      </Card.Text>
    </Card.Body>
  </Card>
  </Col>
  </Row>
  </Container>
  </div>
    )
}
export default App;