import React from 'react'
import{
    Container,Card,Image, Carousel
}from "react-bootstrap";
import ReactPlayer from 'react-player'
import rage from './ragecoffe.mp4'
const App=()=>{
return(
  <Carousel className='slide' fluid controls='' indicators=''style={{height:'500px',overflow:'hidden',margin:'none',height:'fitContent',background:'black'}} >
  <Carousel.Item>
  <ReactPlayer className="player"  playing={true} controls={false} muted playbackRate='1.1' style={{opacity:'0.3',margin:'none'}} loop  height='auto' width='100%'  url={rage}/>
  <Carousel.Caption >
    <h1 className='title'>Award Winning Coffe Made From Natural Ingredients</h1>
  </Carousel.Caption>
</Carousel.Item> 
</Carousel>
)
}
export default App